.stars {
  display: flex;
}

.stars__star {
  width: 16px;
  height: 16px;
  margin-right: 4px;
  background-image: url("../../images/star.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.stars div:nth-child(-n + 4) {
  background-image: url("../../images/star-active.svg");
}
